header {
  background-image: linear-gradient(
      to top,
      rgba($min-color, 0.4),
      rgba(#000, 0.8)
    ),
    url("../images/headerImage.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  .header-container {
    .log {
      display: block;
      width: 13%;
      position: absolute;
      top: 25px;
      left: 0;
      @media (max-width: 991px) {
        width: 19%;
      }
      @media (max-width: 480px) {
        width: 35%;
      }
      @media (max-width: 375px) {
        width: 40%;
      }
      @media (max-width: 320px) {
        width: 47%;
      }
      img {
        width: 100%;
        height: 100%;
      }
    }

    height: 100vh;
    @media (max-width: 991px) {
      height: 97vh;
    }
    @media (max-width: 480px) {
      height: 90vh;
    }

    position: relative;
    width: 100%;
    .content {
      width: 50%;
      @media (max-width: 1024px) {
        width: 70%;
      }
      @media (max-width: 991px) {
        width: 100%;
      }
      @media (max-width: 480px) {
        text-align: center;
      }

      position: absolute;
      top: 50%;
      @media (max-width: 480px) {
        top: 60%;
      }

      left: 0;
      transform: translateY(-50%);
      h1 {
        color: #fff;
        font-family: $font-sacand;
        font-size: 60px;
        letter-spacing: 2px;
        margin-bottom: 25px;
        transition: 0.5s;
        transition-delay: 0.5s;
        opacity: 0;
        transform: translateY(400%);

        @media (max-width: 480px) {
          font-size: 40px;
          margin-bottom: 15px;
        }
        @media (max-width: 375px) {
          font-size: 30px;
        }
      }
      p {
        color: #fff;
        font-size: 20px;
        transition: 0.5s;
        transition-delay: 1s;
        opacity: 0;
        transform: translateY(400%);

        margin-bottom: 6px;
        @media (max-width: 480px) {
          font-size: 17px;
        }
        @media (max-width: 375px) {
          font-size: 15px;
        }
      }
      .icon-list {
        align-items: center;
        transition: 0.5s;
        transition-delay: 1.5s;
        opacity: 0;
        transform: translateY(400%);

        @media (max-width: 480px) {
          text-align: center;
          justify-content: center;
        }

        li {
          &:not(:last-of-type) {
            margin-right: 10px;
          }
          a {
            color: #fff;
            font-size: 17px;
            transition: 0.5s;
            display: block;
            &:hover {
              color: $min-color;
              transform: translateY(1px);
            }
            i {
              backface-visibility: hidden;
            }
          }
        }
      }
      .main-button {
        transition: 0.5s;
        transition-delay: 2s;
        opacity: 0;
        transform: translateY(400%);
      }
      &.active {
        h1,
        p,
        .main-button,
        .icon-list {
          transform: translateX(0);
          opacity: 1;
        }
      }
    }
  }
}
.brands {
  padding: 20px 0 10px;
  background-color: rgba(#ccc, 0.4);
  @media (max-width: 480px) {
    padding: 10px 0 7px;
  }

  ul {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    li {
      width: calc(100% / 5);
      @media (max-width: 480px) {
        width: calc(100% / 2);
        margin-bottom: 10px;
      }

      padding: 0 10px;
    }
  }
}
.about {
  padding: 80px 0;
  background-image: linear-gradient(rgba(#fff, 0.8)),
    url("../images/about-back.jpg");
  background-size: cover;
  background-attachment: fixed;
  @media (max-width: 991px) {
    padding: 50px 0;
  }
  .about-title {
    text-transform: uppercase;
    font-size: 70px;
    font-family: $font-sacand;
    font-weight: 900;
    color: #000;
    @media (max-width: 991px) {
      font-size: 50px;
      margin-bottom: 20px;
      text-align: center;
    }
    @media (max-width: 480px) {
      font-size: 30px;
    }
  }
  p {
    font-size: 17px;
    font-weight: 400;
  }
  .content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    @media (max-width: 480px) {
      text-align: center;
      .main-button {
        margin: auto;
      }
    }
  }
  .image-box {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 15px;
    @media (max-width: 991px) {
      margin-bottom: 16px;
    }
  }
}
.our-story {
  padding: 80px 0;
  @media (max-width: 991px) {
    padding: 40px 0;
  }

  .icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    .story-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
      align-items: center;
      width: calc(100% / 5);
      @media (max-width: 768px) {
        width: calc(100% / 3);
        padding: 0 10px;
      }
      @media (max-width: 480px) {
        width: calc(100% / 2);
      }
      @media (max-width: 480px) {
        width: calc(100% / 2);
      }

      @media (max-width: 425px) {
        width: calc(100% / 1);
      }

      .image-container {
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: block;
        }
      }
      h2 {
        font-size: 30px;
        font-weight: 900;
        height: 30px;
        margin-bottom: 20px;
        font-family: $font-sacand;
        span {
          font-weight: 100;
          font-size: 13px;
          margin-left: 3px;
        }
      }
      h3 {
        font-size: 16px;
        min-height: 34px;
      }
    }
  }
}
.our-digital {
  padding: 60px 0;
  @media (max-width: 991px) {
    padding: 40px 0;
  }
  .content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    .iamge-box {
      width: max-content;
      margin: 5px;
      position: relative;
      overflow: hidden;
      backface-visibility: hidden;
      @media (max-width: 1190px) {
        width: calc(100% / 3);
        height: 200px;
      }

      @media (max-width: 991px) {
        width: 100%;
        margin: 5px 0;
        height: unset;
      }

      img {
        transition: 0.6s;
        display: block;
        @media (max-width: 1190px) {
          height: 100%;
        }

        @media (max-width: 991px) {
          width: 100%;
        }
      }
      img:hover {
        transform: scale(1.03);
      }
      h3 {
        position: absolute;
        top: 10px;
        left: 15px;
        color: #fff;
        font-size: 17px;
      }
    }
  }
}
