.our-latest {
  padding: 60px 0;
  @media (max-width: 991px) {
    padding: 40px 0;
  }

  .item {
    display: flex;
    @media (max-width: 991px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    .image-box {
      width: 50%;
      @media (max-width: 991px) {
        width: 100%;
      }
    }
    .content {
      padding: 10px 20px;
      h2 {
        font-family: $font-sacand;
        font-size: 23px;
      }
      & > span {
        display: block;
        margin-bottom: 10px;
        font-size: 14px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.26);
        padding-bottom: 5px;
      }
      p {
        font-size: 18px;
        margin-bottom: 5px;
      }
      a {
        color: $dark;
        border-bottom: 1px solid $dark;
        transition: 0.5s;
        &:hover {
          color: $min-color;
          border-bottom-color: $min-color;
        }
      }
    }
  }
  .owl-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    .owl-dot {
      width: 10px;
      height: 10px;
      border-radius: 100px;
      background-color: rgba(0, 0, 0, 0.26);
      transition: 0.5s;
      &:not(:last-of-type) {
        margin-right: 10px;
      }
      &.active {
        background-color: $min-color;
        width: 25px;
      }
    }
  }
}
.contact {
  padding: 60px 0;
  background-image: linear-gradient(rgba($min-color, 0.5)),
    url("../images/about-back.jpg");
  background-size: cover;
  background-attachment: fixed;
  @media (max-width: 991px) {
    padding: 30px 0;
  }

  .min-header {
    h2,
    p {
      color: #fff;
    }
    h2::before {
      background-color: #fff;
    }
  }
  .form-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: max-content;
    margin: auto;
    @media (max-width: 991px) {
      width: 100%;
    }
  }

  .top,
  .bottom {
    display: flex;
    @media (max-width: 991px) {
      flex-direction: column;
    }
  }
  .top {
    margin-bottom: 19px;
    @media (max-width: 991px) {
      margin-bottom: 0;
    }
  }
  .input-con {
    width: max-content;
    position: relative;
    @media (max-width: 991px) {
      width: unset;
    }
    &:nth-last-of-type(1) {
      margin-left: auto;
      @media (max-width: 991px) {
        margin-left: unset;
      }
    }
    @media (max-width: 991px) {
      margin-bottom: 12px;
    }

    input {
      box-shadow: 0px 4px 10px 0px rgba(15, 36, 96, 0.09);
      background-color: rgb(255, 255, 255);
      width: 384px;
      height: 51px;
      border-radius: 100px;
      border: 2px solid transparent;
      outline: none;
      font-size: 15px;
      color: rgba($min-color, 0.7);
      padding: 0 66px;
      transition: 0.5s;
      @media (max-width: 991px) {
        width: 100%;
      }
    }
    input:focus {
      border: 2px solid $min-color;
      &::placeholder {
        color: transparent;
      }
    }
    input:focus ~ svg {
      fill: $min-color;
    }
    svg {
      position: absolute;
      left: 30px;
      top: 50%;
      transform: translateY(-50%);
      transition: 0.5s;
      fill: $dark;
      @media (max-width: 991px) {
        right: 31px;
        top: 50%;
      }
    }
  }
  .text-box {
    width: max-content;
    position: relative;
    margin-top: 19px;
    @media (max-width: 991px) {
      width: 100%;
    }
    @media (max-width: 991px) {
      margin-top: 0;
    }
    textarea {
      border-radius: 27px;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 4px 10px 0px rgba(15, 36, 96, 0.09);
      width: 787px;
      height: 105px;
      padding: 24px 70px;
      font-size: 15px;
      color: rgba($min-color, 0.7);
      border: 2px solid transparent;
      outline: none;
      transition: 0.5s;
      @media (max-width: 991px) {
        width: 100%;
      }
    }
    textarea:focus {
      border: 2px solid $min-color;
      &::placeholder {
        color: transparent;
      }
    }
    textarea:focus ~ svg {
      fill: $min-color;
    }

    svg {
      position: absolute;
      left: 30px;
      @media (max-width: 991px) {
        right: 31px;
      }
      top: 50%;
      transform: translateY(-119%);
      transition: 0.5s;
      fill: $dark;
    }
  }
  .btn-container {
    margin-top: 20px;
    display: flex;
    a {
      margin: auto;
    }
  }
}
footer {
  padding: 60px 0 20px;
  background-color: $dark;
  .footer-nav,
  .footer-info {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #a2a1a1;
  }
  .footer-list {
    width: calc(100% / 3);
    @media (max-width: 991px) {
      width: calc(100% / 2);
    }
    @media (max-width: 480px) {
      width: calc(100% / 1);
      text-align: center;
      width: 80%;
      margin: auto;
    }

    padding: 0 20px 10px 0;
    h2 {
      color: #fff;
      font-family: $font-sacand;
      margin-bottom: 20px;
    }
    ul {
      flex-direction: column;
      li {
        margin-bottom: 5px;
        a {
          transition: 0.5s;
          color: #a2a1a1;
          &:hover {
            color: #fff;
          }
          i {
            margin-right: 10px;
          }
        }
      }
    }
  }
  .footer-info {
    border-bottom: unset;
    padding-top: 20px;
  }
  .footer-social {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #a2a1a1;
    @media (max-width: 480px) {
      text-align: center;
    }

    ul {
      li {
        &:not(:last-of-type) {
          margin-right: 10px;
        }
        a {
          color: #fff;
          font-size: 17px;
          transition: 0.5s;
          display: block;
          &:hover {
            color: $min-color;
            transform: translateY(1px);
          }
          i {
            backface-visibility: hidden;
          }
        }
      }
    }
  }
}
