.main-button {
  display: inline-block;
  font-size: 18px;
  position: relative;
  transition: 0.5s;
  backface-visibility: hidden;
  padding: 10px 20px;
  min-width: 155px;
  color: #fff;
  background-color: $min-color;
  margin-right: auto;
  font-size: 16px;
  letter-spacing: 1.2px;
  border-radius: 100px;
  box-shadow: 0 0 0 0 rgba($min-color, 1);
  transition: all 0.5s ease-in-out;
  font-weight: 700;
  position: relative;
  text-align: left;

  i {
    position: absolute;
    font-size: 13px;
    top: 50%;
    transform: translateY(-50%);
    right: 15.5%;
    display: flex;
    height: 100%;
    align-items: center;
    justify-self: center;
    &:nth-of-type(2) {
      transition: 0.5s;
      right: 10%;
      opacity: 0;
    }
  }
  &:hover {
    text-decoration: none;
    color: #fff;
    transform: translateX(6px);
    animation: btnAnimation 0.8s cubic-bezier(0.66, 0, 0, 1);
    i {
      opacity: 1;
      animation: color-anim 1s infinite 0.4s;
      &:nth-of-type(2) {
        animation: color-anim 1s infinite 0.6s;
      }
    }
  }
}
// animation
@keyframes btnAnimation {
  to {
    box-shadow: 0 0 0 8px rgba($min-color, 0);
  }
}

@keyframes color-anim {
  0% {
    color: white;
  }
  50% {
    color: rgba(#fff, 0.7);
  }
  100% {
    color: #fff;
  }
}
