.nav-trigger {
  background: $min-color;

  position: fixed;
  z-index: 200;
  right: 7.7%;

  @media (max-width: 991px) {
    right: 13.7%;
  }
  @media (max-width: 768px) {
    right: 3.2%;
  }
  @media (max-width: 480px) {
    right: 1.7%;
  }
  top: 20px;
  width: 55px;
  height: 55px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 50%;
  transition: 0.5s;
  cursor: pointer;
  span {
    width: 28px;
    display: block;
    height: 3px;
    background-color: #fff;
    border-radius: 120px;
    transition: 0.5s;
    &:not(:last-of-type) {
      margin-bottom: 5px;
    }
  }
  &.active {
    background-color: #fff;
    span {
      background-color: $min-color;

      &:nth-child(1) {
        transform: translateY(8px) rotate(135deg);
      }
      &:nth-child(2) {
        transform: rotate(135deg);
        opacity: 0;
      }
      &:nth-child(3) {
        transform: translateY(-8px) rotate(-135deg);
      }
    }
  }
  // &.scrolled {
  //   background-color: min-color;
  //   transform: scale(1);
  //   span {
  //     background-color: #fff;
  //   }
  // }
}

.menu {
  position: fixed;
  top: 0;
  // left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  transition: all 0.38s;
  z-index: 100;
  background-color: rgba(#000000, 0.7);
  padding-right: 15%;
  @media (max-width: 991px) {
    padding-right: 19%;
  }

  @media (max-width: 768px) {
    padding-right: 25%;
  }
  @media (max-width: 480px) {
    padding-right: 0;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  &::before {
    content: "";
    position: fixed;
    top: 0;
    right: 0;
    width: 30%;
    @media (max-width: 991px) {
      width: 40%;
    }
    @media (max-width: 768px) {
      width: 60%;
    }
    @media (max-width: 480px) {
      width: 100%;
    }

    height: 100%;
    background: $min-color;
    transform-origin: 0 0;
    transform: skew(0) translateX(120%);

    transition: all 0.275s 0.1s;
    z-index: 1;
  }
  ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 20;
    margin-bottom: 0;
    padding-inline-start: 0;
    li {
      &:not(:last-of-type) {
        margin-bottom: 16px;
      }
      a {
        font-size: 20px;
        color: #fff;
        opacity: 1;
        text-decoration: none;
      }
      opacity: 0;
      transform: translateX(50px);
      transition: all 0.5s;
    }
  }
  &.active {
    opacity: 1;
    visibility: visible;
    &::before {
      transform: skew(-12deg) translateX(0%);
      @media (max-width: 480px) {
        transform: skew(0) translateX(0%);
      }
    }
    ul li {
      opacity: 1;
      &:nth-of-type(1) {
        transition: all 275ms 175ms;
        transform: translateX(0);
      }
      &:nth-of-type(2) {
        transition: all 275ms 225ms;
        transform: translateX(0);
      }
      &:nth-of-type(3) {
        transition: all 275ms 275ms;
        transform: translateX(0);
      }
      &:nth-of-type(4) {
        transition: all 275ms 325ms;
        transform: translateX(0);
      }
      &:nth-of-type(5) {
        transition: all 275ms 380ms;
        transform: translateX(0);
      }
      &:nth-of-type(6) {
        transition: all 275ms 425ms;
        transform: translateX(0);
      }
      &:nth-of-type(7) {
        transition: all 275ms 475ms;
        transform: translateX(0);
      }
      &:nth-of-type(8) {
        transition: all 275ms 525ms;
        transform: translateX(0);
      }
      &:nth-of-type(9) {
        transition: all 275ms 575ms;
        transform: translateX(0);
      }
      &:nth-of-type(10) {
        transition: all 275ms 625ms;
        transform: translateX(0);
      }
    }
  }
}
