.min-header {
  text-align: center;
  margin-bottom: 40px;
  h2 {
    font-family: $font-sacand;
    font-size: 50px;
    position: relative;
    width: 100%;
    color: $dark;
    margin-bottom: 30px;
    @media (max-width: 480px) {
      font-size: 40px;
    }
    @media (max-width: 320px) {
      font-size: 30px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: -17px;
      left: 50%;
      transform: translateX(-50%);
      width: 80px;
      height: 3px;
      background-color: $min-color;
    }
  }
  p {
    font-size: 25px;
    width: 55%;
    margin: auto;
    letter-spacing: 1.1px;
    font-style: italic;
    text-align: center;
    color: $dark;
    @media (max-width: 991px) {
      width: 100%;
    }
    @media (max-width: 480px) {
      font-size: 17px;
    }
  }
}
