ï»¿html {
  overflow-y: hidden;
}
body {
  font-family: $font-mid;
  direction: ltr;
  overflow-x: hidden;
  &.overflow {
    overflow-y: hidden;
  }
  &::-webkit-scrollbar {
    background-color: $min-color;
    width: 7px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #eff3f5;
    border-radius: 100px;
  }
}
.container {
  position: relative;
}
* {
  font-family: inherit;
}
a {
  &:hover {
    color: currentColor;
  }
}
input,
a,
button {
  font-family: $font-mid;

  &:focus {
    outline: none;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
  }
}

button {
  cursor: pointer;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}
